import { TableRowColumn } from './table.api_row_column.abstract';

/**
 * 行对象
 */
export class TableRow extends TableRowColumn {
    /**
     * 获取列对象的统一纵坐标
     * @returns 坐标
     */
    public getUnifiedPosition () {
        return {
            y: this.unifiedPosition,
        };
    }
    
    /**
     * 设置行高
     * @param height 行高
     */
    public setHeight (height: number) {
        this.size = height;
        
        this.externalContext.emitCellEvent('CELL_SET_SIZE', {
            y: this.index - 1,
            height,
        });
    }

    /**
     * 获取行高
     * @returns 行高
     */
    public getHeight () {
        return this.size;
    }

    /**
     * 获取第col列的单元格
     * @param col 行
     * @returns 单元格
     */
    public getCell(col: number) {
        return this.externalContext.getCell(col, this.index);
    }
}
